import React, { Component } from 'react';
import OutputItem from './output.js';
import { SearchInput } from 'fundamental-react/lib/SearchInput';
import { Button } from 'fundamental-react/lib/Button';
import Sentiment from 'sentiment';

class Feed extends Component {

    constructor(props) {
        super(props);
        this.state = {outputListState: [], textInput: ""};
        this.onSubmitBtnClick = this.onSubmitBtnClick.bind(this);
        this.onClearBtnClick = this.onClearBtnClick.bind(this);
    }

    importAll(r) {
        let images = {};
        r.keys().map((item) => { return images[item.replace('./', '')] = r(item); });
        return images;
      }
      
      
      nlpAnalysis(txt) {    
          const cSentiment = new Sentiment();
          let oOutput = cSentiment.analyze(txt)
          
          oOutput.comparative = Math.abs(oOutput.comparative) > 1 ? Math.abs(oOutput.comparative) / oOutput.comparative : oOutput.comparative

          let sColrOutput = this.colrConv(oOutput)
        let sEmojiFile = this.emojiLookup(oOutput)
        
        return [sColrOutput, oOutput.comparative, sEmojiFile]
    }
    
    emojiLookup(ipt) {
        const oEmojiObject = {
            stateneg3: "emoji_-3.png", 
            stateneg2: "emoji_-2.png", 
            stateneg1: "emoji_-1.png", 
            stateneu: "emoji_0.png", 
            statepos1: "emoji_1.png", 
            statepos2: "emoji_2.png", 
            statepos3: "emoji_3.png"
        }
        
        const images = this.importAll(require.context('../img', false, /\.(png|jpe?g|svg)$/));

        let scr = ipt.comparative
        
        if (scr > 0.67) {
            return  images[oEmojiObject.statepos3]
        } else if (scr > 0.33) {
            return  images[oEmojiObject.statepos2]
        } else if (scr > 0) {
            return  images[oEmojiObject.stateneg1]
        } else if (scr === 0) {
            return  images[oEmojiObject.stateneu]
        } else if (scr < -0.67) {
            return  images[oEmojiObject.stateneg3]
        } else if (scr < -0.33) {
            return  images[oEmojiObject.stateneg2]
        } else if (scr < 0) {
            return  images[oEmojiObject.stateneg1]
        } else {
            return
        }

    }

    colrConv(ipt) {

        let scr = ipt.comparative
        let colrDiff = 155 * Math.abs(scr)

        let hexcodeConv = `rgb(${scr < 0 ? 100 + colrDiff : 100 }, ${scr > 0 ? 100 + colrDiff : 100 }, 100)`

        return hexcodeConv

    }

    onSubmitBtnClick(event) {

        let inputText = this.state.textInput;

        switch (inputText) {
            case "":
                break;
            default:
                let nlpOutput = this.nlpAnalysis(inputText);
                let outputItemComp = <OutputItem key={this.state.outputListState.length} textInput={inputText} SAValue={nlpOutput[0]} score={nlpOutput[1]} emoji={nlpOutput[2]}/>;

                this.setState((state) => {

                    let output = [outputItemComp].concat(state.outputListState)

                    return {outputListState: output}
                });
    }}

    onClearBtnClick(event) {

        this.setState((state) => {

            return {outputListState: []}
        });

    }

    render() {
      return (
          <div>
            <InputBar />
          </div>
        )
    }
}

class InputBar extends Feed {

    updateInputValue(evt) {
        this.setState({
            textInput: evt.target.value
    })};

    clearInput() {
        this.setState((state) => {
            return {textInput: ""}
        })
    };

    render() {
      return (
        <div>
        <div className="outputContainer">
            {this.state.outputListState}        
        </div>
        <div className="inputBox">
            <SearchInput
            noSearchBtn
            id="inputBox"
            onEnter={() => {                
                this.onSubmitBtnClick()
                this.clearInput()
                return
            }}
            className="Box"
            value={this.state.textInput}
            onChange={evt => this.updateInputValue(evt)}
            />
            <Button className="Btn" type="standard" onClick={this.onSubmitBtnClick}>
              Analyze
            </Button>
            <Button className="Btn" type="standard" onClick={this.onClearBtnClick} disabled={this.state.outputListState.length > 0 ? false : true}>
              Clear Feed
            </Button>
        </div>
        </div>
        )
    }
}
  
export default Feed