import React, { Component } from 'react';
import Tile from 'fundamental-react/lib/Tile/Tile.js';
import Image from 'fundamental-react/lib/Image/Image.js';

class OutputItem extends Component {

  render() {
      return (        
        <Tile className="outputLine" style={{background: this.props.SAValue}}>
        <Tile.Media>
          <Image
              photo={this.props.emoji}
              size="m"
              type="circle"
            />
        </Tile.Media>

          <Tile.Content title={this.props.textInput} className="outputLineCnt">
            <p>
              {this.props.score.toFixed(2)}
            </p>
          </Tile.Content>
        </Tile>
        )
    }
  }
  
export default OutputItem